.score-board{
    width: 320px;
    display:grid;
    grid-auto-flow: column;
    margin-top: 5px;
}

.score-board div{
    color: white;
    text-align: center;
    font-size: 2em;
    padding:5px;
    font-weight:bold;
    background-color:dodgerblue;
}

.score-board div:first-child{
    background-color:deeppink;
}